.hero-banner {
	position: relative;
	padding: 80px 0;

	.banner-content {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: flex-start;
		padding: 60px 0;
		max-width: 695px;
		@include breakpoint(wide) {
			max-width: 50%;
		}
		@include breakpoint(medium) {
			max-width: 80%;
		}

		h4 {
			background: $soft_blue;
			color: $black;
			display: inline-block;
			padding: 5px 15px 8px;
		}

		h1 {
			margin: 0 0 30px;
			font-weight: 300;
			color: $color_text;
			@include breakpoint(medium) {
				color: $white;
			}

			strong {
				font-weight: 500;
			}

			span {
				font-weight: 500;
				color: $red;
				border-right: solid $red 7px;
				animation: cursor 1s ease-in-out infinite;
			}
		}

		p {
			font-size: 22px;
			max-width: 750px;
			margin: 0 0 25px;
		}
	}

	.banner-trans {
		position: absolute;
		top: 0;
		right: 30px;
		z-index: 1;
		pointer-events: none;
		width: 600px;
		min-height: 500px;
		height: calc(100% - 40px);
		@media screen and (max-width: 1299px) {
			width: 45%;
		}
		@include breakpoint(medium) {
			height: 100%;
			width: 100%;
			z-index: -1;			
		}
		&:after {
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			width: 100%;
			background-color: rgba($black, 0.6);
			background-blend-mode: multiply;
			@include breakpoint(medium) {
				content: '';
			}
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			animation: border-transform-default 10s linear infinite alternate forwards;
			@include breakpoint(medium) {
				animation: none;
			}
		}
	}
}
.services-section {
	.row {
		@include breakpoint(wide) {
			justify-content: center;
		}
		.col-lg-4 {
			margin-bottom: 30px;
			@media screen and (max-width: 1199px) and (min-width: 992px) {
				flex: 50%;
				max-width: 50%;
			}
		}
	}
}
.service-card {
	height: 100%;
	.card-inner {
		background: $white;
		transition: 0.5s;
		padding: 60px 40px;
		border-radius: 7px;		
		border: 1px solid #eeeff1;
		height: 100%;

		.card-image {
			text-align: center;

			img {
				margin: 0 0 30px;
			}
		}

		.card-content {
			text-align: center;
			display: flex;
			flex-direction: column;
			justify-content: space-between;

			h3,
			p {
				margin: 0 0 20px;
			}

			h3 {
				font-weight: 700;

				@media screen and (min-width: 1200px) {
					font-size: 26px;
				}

				a {
					@include transition(0.4s);
				}
			}

			p {
				line-height: 2;
				color: $color_text;
				font-size: 16px;
			}

			.button-wrap {}
		}

		&:hover {
			box-shadow: 20px 0 60px rgba(14, 27, 56, 0.15);

			.card-content {
				h3 {
					a {
						color: $red;
					}
				}
			}
		}
	}

	&.style-2 {
		height: 100%;

		.card-inner {
			background: transparent;
			position: relative;
			padding: 0;
			border: 0;
			height: 100%;
			box-shadow: unset;
			margin: 0;

			.card-image {
				text-align: left;

				img {
					width: auto;
					height: 60px;
					margin: 0 0 20px;
				}
			}

			.card-content {
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				text-align: left;
				height: 100%;

				h3 {}

				p {
					line-height: 1.77;
					margin: 0;
				}
			}
		}
	}
}

.clients-section {
	padding: 60px 0;
	background: rgba($grey, 0.07);

	.clients-logo-wrap {
		.section-title {
			margin-left: 0;
			text-align: left;
			max-width: 100%;

			h3 {
				color: $color_text;
				font-weight: 500;
				position: relative;
				display: inline-block;
				padding-left: 55px;

				@media screen and (min-width: 1200px) {
					font-size: 26px;
				}

				&:after {
					content: '';
					position: absolute;
					top: 55%;
					transform: translateY(-50%);
					height: 3px;
					width: 40px;
					background: $red;
					left: 0;
				}
			}
		}

		.clients-slider {
			.client-logo {
				padding: 0 15px;

				a {
					display: block;
					text-align: center;

					img {
						padding: 10px 0;
						margin: 0 auto;
						@include transition(0.5s);
						transform: scale(1);
					}

					&:hover {
						img {
							transform: scale(1.1);
						}
					}
				}
			}
		}
	}
}

.home-about-section,
.left-content-right-image {
	background-color: rgba($grey, 0.1);
	padding: 100px 0 60px;

	.content-left {
		padding-left: calc((100vw - 1285px) / 2);

		.section-title {
			text-align: left;
			margin-left: 0;
			margin-bottom: 35px;

			h5 {
				padding-left: 0;

				&:before {
					content: none;
				}
			}
		}

		.about-content {
			p {
				font-size: 18px;
				line-height: 1.55;
				margin: 0 0 20px;
			}
		}

		.button-wrap {
			padding-top: 20px;
		}
	}

	.company-stats {
		padding-left: 45px;
		padding-right: 15px;

		.col-lg-6 {
			margin: 0 0 20px;

			&:nth-child(2) {
				.stats-box {
					margin-top: 100px;
					@include breakpoint(wide) {
						margin-top: 50px;
					}
				}
			}

			&:nth-child(odd) {
				.stats-box {
					margin-top: -100px;
					@include breakpoint(wide) {
						margin-top: -30px;
					}
				}
			}

			&:first-child {
				.stats-box {
					margin-top: 0;
				}
			}
		}

		.stats-box {
			border-radius: 14px;
			padding: 40px 30px;
			min-height: 320px;
			background-color: $white;
			@include transition(0.4s);
			@include breakpoint(wide) {
				padding: 30px 20px;
				min-height: auto;
			}

			.sub-heading,
			h2 {
				margin: 0 0 20px;
			}

			.sub-heading {
				display: block;
			}

			h2 {
				color: $color_text;

				span {
					font-weight: 900;
					color: $color_text;
				}
			}

			p {
				// font-size: 18px;
				color: $color_text;				
			}

			&:hover {
				box-shadow: 0 10px 15px 0 rgba(241, 99, 52, 0.1)
			}
		}
	}
}

.service-cat-section {
	padding-bottom: 40px;

	.row {
		.col-lg-4 {
			margin: 0 0 30px;
		}
	}
}

.service-cat-card {
	.card-inner {
		padding: 30px 20px;
		position: relative;
		border-radius: 10px;
		position: relative;
		@include transition(all 0.3s ease-out 0s);
		border: 1px solid rgba($grey, 0.25);
		background: $white;

		&:before {
			position: absolute;
			content: '';
			// background: linear-gradient(260.52deg, $navy_blue -4.98%, $red 96.9%);
			background: linear-gradient(260.52deg, rgba($grey, 0.2) -4.98%, rgba($grey, 0.4) 96.9%);
			border-radius: 10px;
			right: 0;
			bottom: 0;
			width: 100%;
			height: 100%;
			z-index: -1;
			visibility: hidden;
			opacity: 0;
			@include transition(all 0.3s ease-out 0s);
		}

		.card-image {
			margin: 0 0 20px;

			img {
				width: 80px;
				margin: 0 auto;
				display: block;
				transform: translateY(0px);
				@include transition(0.4s);
			}
		}

		.card-content {
			text-align: center;

			h4 {
				margin: 0 0 15px;
				color: $navy_blue;
				font-weight: 700;
			}

			h5 {
				color: $color_text;

				span {
					color: $red;
					font-weight: 700;
				}
			}

			.features {
				padding: 15px 0 0;
				text-align: left;
				margin: 0 0 30px;

				ul {
					li {
						position: relative;
						padding-left: 20px;
						line-height: 1.77;
						color: $color_text;

						&:before {
							content: '';
							position: absolute;
							top: 10px;
							left: 0;
							height: 8px;
							width: 8px;
							border-radius: 50%;
							background: $red;
						}
					}
				}
			}
		}

		&:hover {
			&:before {
				transform: rotate(-6.05deg);
				visibility: visible;
				opacity: 0.85;
			}

			.card-image {
				img {
					transform: translateY(-10px);
				}
			}

			.card-content {
				.three-dot {
					span {
						background: $red;
					}
				}
			}
		}
	}
}

.why-choose-us,
.faq-section {
	padding: 80px 15px;
	overflow: hidden;

	.why-choose-content,
	.faq-content {
		max-width: 600px;
		margin: 0 0 0 auto;

		.list-content {
			ul {
				li {
					padding-left: 40px;
					position: relative;

					&:not(:last-child) {
						margin: 0 0 25px;
					}

					&:before {
						content: '';
						position: absolute;
						top: 0;
						left: 0;
						height: 24px;
						width: 24px;
						background-image: url('../../images/tick-circle-navy-blue.svg');
						background-size: contain;
						background-repeat: no-repeat;
					}

					h5 {
						font-size: 20px;
						color: $navy_blue;
						margin: 0 0 8px;
					}
				}
			}
		}

		p {
			font-size: 18px;
			line-height: 1.55;
			margin: 0 0 20px;
		}
	}

	.faq-image,
	.why-choose-image {
		width: calc(100% + 15px);
		position: relative;
	}
}

.why-choose-us {
	.container-wrapper {
		padding-right: calc((100vw - 1270px) / 2);
	}

	.why-choose-image {
		left: -15px;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&.image-right {
		.container-wrapper {
			padding-right: 0;
			padding-left: calc((100vw - 1270px) / 2);

			.row {
				.col-lg-6 {
					&:first-child {
						order: 2;
					}

					&:last-child {
						order: 1;
					}
				}
			}

			.why-choose-image {
				left: 15px;
			}

			.why-choose-content {
				margin: 0;
				max-width: 100%;

			}
		}
	}
}

.faq-section {
	.container-wrapper {
		padding-left: calc((100vw - 1270px) / 2);
	}

	.faq-image {
		text-align: right;

		// right: -15px;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	.faq-content {
		margin-left: 0;

		.accordion-wrap {
			.accordion-list {
				&:not(:last-child) {
					margin: 0 0 20px;
				}

				.title {
					h4 {
						color: $navy_blue;
						position: relative;
						margin: 0;
						cursor: pointer;
						padding-left: 30px;

						@media screen and (min-width: 1200px) {
							font-size: 20px;
						}

						&:before,
						&:after {
							content: '';
							position: absolute;
							background: $navy_blue;
							@include transition(0.4s);
						}

						&:before {
							top: 12px;
							left: 0px;
							height: 2px;
							width: 17px;
						}

						&:after {
							left: 8px;
							top: 4px;
							width: 2px;
							height: 17px;
						}
					}
				}

				.content {
					padding: 15px 0 20px 30px;
					display: none;

					p {
						margin: 5px 0;
						line-height: 1.77;
					}
				}

				.title {
					h4 {
						&.icon-open {
							text-decoration: underline;
							color: $red;

							&:after {
								opacity: 0;
								visiblity: hidden;
							}
						}
					}
				}
			}
		}
	}
}

.our-work-section {
	.project-wrapper {
		padding-bottom: 20px;

		.project-filter {
			padding: 0 0 40px;

			ul {
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				justify-content: center;

				li {
					position: relative;

					&:after {
						position: absolute;
						content: "/";
						right: -6px;
						top: 0;
						bottom: 0;
						margin: auto 0;
						color: #4b4b4b;
					}

					&:last-child {
						&:after {
							content: none;
						}
					}

					a {
						display: inline-block;
						cursor: pointer;
						position: relative;
						color: $color_text;
						font-weight: 400;
						font-size: 18px;
						padding: 0 32px;

						&.active {
							color: $red;
							font-weight: 700;
						}
					}
				}
			}
		}

		.project-grid {
			display: flex;
			flex-wrap: wrap;
			margin: 0 -15px;

			.project-card {
				flex: 0 0 33.33%;
				max-width: 33.33%;
				padding: 0 15px;
				margin: 0 0 30px;

				.card-inner {
					position: relative;
					box-shadow: 0px 0px 30px rgba($black, 0.15);

					.stretched-link {
						position: absolute;
						top: 0;
						left: 0;
						height: 100%;
						width: 100%;
						z-index: 2;
						font-size: 0px;
					}

					.card-image {
						overflow: hidden;

						img {
							width: 100%;
							@include transition(0.6s);
							// transition-delay: 0.2s;
							transform: scale(1);
						}
					}

					.card-hover {
						// z-index: 1;
						// position: absolute;
						// left: 30px;
						// top: 30px;
						// background: rgba($white, 0.9);
						// display: flex;
						// align-items: center;
						// justify-content: center;
						// width: calc(100% - 60px);
						// height: calc(100% - 60px);
						// transition: all .3s;
						// opacity: 0;
						// pointer-events: none;
						// visibility: hidden;
						// transform: translateY(30px);
						padding: 20px;

						.hover-content {
							text-align: center;

							h3 {
								color: $red;
								line-height: 1.33;
								font-weight: 700;
							}

							p {
								color: $color_text;
								margin: 0 0 5px;
							}
						}
					}

					&:hover {
						.card-image {
							img {
								transform: scale(1.1);
							}
						}

						.card-hover {
							transform: translateY(0);
							opacity: 1;
							pointer-events: all;
