.testimonial-section {
	padding: 40px 0 80px;

	.testimonial-left {
		.section-title {
			margin-left: 0;
			text-align: left;

			h5 {
				padding-left: 0;

				&:before {
					content: none;
				}
			}
		}
	}

	.testimonial-wrapper {
		.testimonial-img-slider {
			width: 315px;
			margin-right: auto;
			margin-left: 0;

			.img-holder {
				position: relative;
				display: block;
				overflow: hidden;
				transition: all 500ms ease;
				width: 92px;
				cursor: pointer;

				&:before {
					content: '';
					position: absolute;
					top: 0;
					left: 0;
					bottom: 0;
					right: 0;
					background-color: rgba(var(--moniz-base-rgb, 255, 255, 255), 0.3);
					z-index: 1;
					transition: all 500ms ease;
				}

				&:after {
					content: '';
					position: absolute;
					bottom: 0;
					right: 10px;
					height: 35px;
					width: 35px;
					background-color: $red;
					border-radius: 50%;
					display: flex;
					align-items: center;
					justify-content: center;
					transform: scale(0);
					transition: all 500ms ease;
					background-image: url(../../images/testimonials-1-quote.png);
					background-repeat: no-repeat;
					background-position: center center;
				}

				img {
					width: 92px;
					border-radius: 50%;
					transition: all 0.5s ease-in-out 0.6s;
				}

				&.slick-current {
					&:before {
						background: rgba(255, 255, 255, 0);
					}

					&:after {
						transform: scale(1);
					}
				}
			}
		}

		.testimonial-slider {
			.testimonial-block {
				position: relative;
				display: block;

				.content {
					padding-top: 30px;

					p {
						font-size: 26px;
						color: $navy_blue;
						font-weight: 400;
						line-height: 1.77;
						margin: 0;
					}
				}

				.client-info {
					padding-top: 35px;

					h4 {
						color: $red;
						margin: 0;
						font-weight: 700;
					}

					span {
						font-weight: 500;
						color: $color_text;
						display: block;
					}
				}
			}
		}

		.slick-dots {
			position: absolute;
			bottom: 0;
			right: 10px;

			li {
				display: inline-block;
				margin: 0 0 0 10px;

				button {
					width: 10px;
					height: 10px;
					background-color: rgba(30, 30, 34, 0.2);
					border: 2px solid;
					border-color: transparent;
					transition: all 500ms ease;
					opacity: 1;
					margin: 0;
					display: block;
					font-size: 0;
					border-radius: 50%;
					padding: 0;
				}

				&.slick-active {
					button {
						background-color: transparent;
						border-color: $red;
					}
				}
			}
		}
	}
}

.awards-section {
	// background: rgba($grey, 0.15);
	overflow: hidden;

	.awards-marquee {
		&:after {
			content: '';
			display: block;
			clear: both;
		}
	}

	.awards-wrapper {
		display: flex;
		align-self: stretch;

		.awards-block {
			padding: 0 10px;
			height: 140px;
			width: auto;
			height: auto;

			.block-inner {
				display: flex;
				align-items: center;
				justify-content: center;
				padding: 10px;
				background: $white;
				border: 1px solid #eee;
				height: 100%;

				img {
					// height: 100%;
					mix-blend-mode: multiply;
					@include transition(0.4s);
				}

				&:hover {
					img {
						mix-blend-mode: luminosity;
					}
				}
			}
		}
	}
}

.blog-section {
	padding: 60px 0 10px;

	.section-title {
		margin-bottom: 45px;
	}
	.row {
		.col-lg-4 {
			margin: 0 0 40px;
		}
	}
	.blog-card {
		.card-inner {
			.card-image {
				padding: 0 0 20px;

				a {
					display: block;
				}

				img {
					width: 100%;
				}
			}

			h5 {
				text-align: center;
				width: 90%;
				margin: 0 auto;

				a {
					color: #444444;
					@include transition(0.4s);

					&:hover {
						color: $red;
					}
				}
			}
		}
	}
}

.home-contact-section {
	.row {
		margin: 0;
		align-self: center;

		.col-lg-7,
		.col-lg-5 {
			padding: 0;
		}

		.contact-left {
			position: relative;
			min-height: 450px;
			height: 100%;
			padding: 30px;

			&:before {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				height: 100%;
				width: 100%;
				background: rgba($color_text, 0.8);
				z-index: 0;
				pointer-events: none;
			}

			.content-inner {
				overflow: hidden;
				@media screen and (max-width: 1299px) {
					height: 100%;
				}
			}

			.bg-image {
				position: absolute;
				top: 0;
				left: 0;
				z-index: -1;
				height: 100%;
				width: 100%;
				object-fit: cover;
			}

			ul {
				position: relative;
				z-index: 1;
				display: flex;
				flex-direction: column;
				@media screen and (max-width: 1299px) {
					height: 100%;
					justify-content: center;
				}

				li {
					display: inline-block;
					padding-bottom: 50px;
					overflow: hidden;
					width: 86%;
					position: relative;
					@media screen and (max-width: 1299px) {
						width: 100%;
						text-align: center;
					}
					@include breakpoint(ipadpro) {
						padding-bottom: 35px;
					}
					
					&:before {
						content: '';
						position: absolute;
						height: 20px;
						width: 3px;
						right: 0;
						top: 0;
						background: $white;
					}

					&:after {
						content: '';
						position: absolute;
						bottom: 0;
						left: calc(14% + 13px);
						height: 45px;
						width: 3px;
						background: $white;
					}

					&:before,
					&:after {
						@media screen and (max-width: 1299px) {
							content: none;
						}
					}

					h3 {
						position: relative;
						color: $white;
						font-size: 26px;
						margin: 0;
						font-weight: 400;
						display: inline-block;
						@include breakpoint(ipadpro) {
							font-size: 22px;
						}

						&:before {
							content: '';
							position: absolute;
							top: 20px;
							left: calc(100% + 10px);
							height: 3px;
							width: 400%;
							background: $white;
							@media screen and (max-width: 1299px) {
								content: none;
							}
						}

						span {
							color: red;
							font-size: 32px;
							font-weight: 500;
						}
					}

					&:first-child {
						&:before {
							content: none;
						}

						h3 {
							&:before {
								content: none;
							}
						}
					}

					&:last-child {
						@media screen and (max-width: 1299px) {
							padding-bottom: 0;
						}
						&:after {
							content: none;
						}
					}

					&:nth-child(even) {
						text-align: right;
						margin-left: auto;
						@media screen and (max-width: 1299px) {
							text-align: center;
						}

						&:before {
							left: 0;
							right: unset;
						}

						&:after {
							left: unset;
							right: calc(14% + 13px);
						}

						h3 {
							&:before {
								left: unset;
								right: calc(100% + 10px);
							}
						}
					}
				}
			}
		}

		.contact-form-right {
			background-color: $red;
			height: 100%;

			.form-inner {
				padding: 30px;

				.row {
					margin: 0 -15px;

					.col-12 {
						margin: 0 0 12px;
					}
				}

				input,
				select,
				textarea,
				.form-control {
					color: $white;
					font-weight: 400;

					&::-webkit-input-placeholder {
						color: $white;
					}

					&::-moz-placeholder {
						color: $white;
					}

					&:-ms-input-placeholder {
						color: $white;
					}

					&:-moz-placeholder {
						color: $white;
					}
				}

				input {

					&:not([type=submit]),
					{
					background: transparent;
				}
			}

			select,
			textarea {
				background: transparent;
			}

			select {
				background-image: url('../../images/arrow-down-white.svg');
				background-size: 20px;
				background-position: calc(100% - 10px) 50%;
				background-repeat: no-repeat;
				option {
					color: $color_text;
				}
			}

			.input-submit {
				padding-top: 15px;

				input {
					background: $white;
					border-radius: 3px;
					color: $red;
					font-size: 16px;
					height: auto;
					padding: 10px 30px;
					font-family: $font_rubik;
					font-weight: 500;

					&:hover {
						background: $navy_blue;
						border-color: $navy_blue;
						color: $white;
					}
				}
			}

			.wpcf7-not-valid-tip {
				color: $purple;
			}
		}
	}
}
}